.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-size: 16px;
  font-family: "Montserrat", "Helvetica", sans-serif;
  font-weight: 600;
  line-height: 1.4;
  color: #fff;
  background-color: #00bdca;
  min-width: 320px;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: #fff;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0;
}

ul, li {
  padding: 0;
  margin: 0;
  list-style: none;
}

.container {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
}

.block {
  display: flex;
}

.header {
  display: flex;
  justify-content: space-between;
  margin: 60px 65px 60px 107px;
}

.header__logo a {
  display: flex;
  align-items: center;
}

.header__logo a img {
  width: 44px;
  height: 44px;
  margin-right: 12px;
}

.title {
  line-height: 1;
  font-size: 13px;
  font-weight: 500;
}

.title h1 {
  font-size: 24px;
  font-weight: 600;
}

.title h1 span {
  color: #56ecf6;
}

.navigation {
  display: flex;
  align-items: center;
  height: 50px;
}

.navigation__item {
  margin-right: 34px;
}

.navigation__item--sign {
  background-color: #fff;
  color: #1d354e;
  padding: 13px 25px;
  border-radius: 16px;
  margin-right: 10px;
}

.navigation__item--lang {
  margin: 0;
}

.lang_menu {
  background-color: #fff;
  width: 80px;
  min-height: 50px;
  border-radius: 21px;
  text-align: center;
}

.lang_menu__item {
  color: #1d354e;
  line-height: 50px;
}

.lang_extra {
  display: none;
}

.button {
  display: inline-block;
  border: none;
  color: #fff;
  text-decoration: none;
  background-color: #1d354e;
  padding: 22px 67px;
  font-size: 18px;
  font-weight: 600;
  font-family: "Montserrat", "Helvetica", sans-serif;
  border-radius: 32px;
  text-align: center;
  outline: none;
  cursor: pointer;
}

.button:focus, .button:hover {
  text-decoration: none;
  color: #fff;
  outline: none;
}

.button:hover {
  background-color: #244261;
}

.button:active {
  background-color: #16283b;
}

.section_robot {
  background: url("../../img/robot2.png");
  background-repeat: no-repeat;
  background-position: 37% 0%;
  height: 1450px;
  position: relative;
  top: -76px;
}

.container--relative {
  position: relative;
}

.robot {
  text-align: center;
  width: 600px;
  position: absolute;
  right: 75px;
  top: 135px;
}

.robot h2 {
  font-size: 72px;
  font-weight: 600;
  line-height: 1.2;
}

.robot p {
  font-size: 30px;
  font-weight: 600;
  margin: 5px 0 20px;
  line-height: 1.7;
}

.tablet__wrapper {
  width: 100%;
  background: #fff;
  height: 720px;
  border-radius: 16px;
  position: absolute;
  top: 705px;
  z-index: -1;
  margin-left: 7px;
}

.tablet__inner {
  width: 100%;
  max-width: 1074px;
  height: 624px;
  border-radius: 8px;
  margin: 48px auto;
  background: #222222;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bg {
  width: 400%;
  height: 142%;
  transform: rotate(30deg);
  background: #1d354e;
  position: absolute;
  left: -140%;
  top: 1076px;
  z-index: -2;
}

.robot__box1 {
  position: absolute;
  top: 300px;
  left: 7%;
}

.robot__box2 {
  position: absolute;
  top: 455px;
  right: 10%;
}

.forecast {
  max-width: 1000px;
  margin: 0 auto;
  position: relative;
}

.forecast h3 {
  font-size: 60px;
  font-weight: 600;
  margin: 41px 0 0 23px;
}

.forecast p {
  font-size: 24px;
  font-weight: 600;
  margin-left: 23px;
  margin-top: 2px;
}

.currency {
  display: flex;
  margin-top: 40px;
  margin-left: 23px;
}

.currency li {
  margin-right: 30px;
  width: 136px;
  height: 84px;
}

.currency a {
  display: block;
  text-align: center;
  text-transform: uppercase;
  border: 8px solid #223e5c;
  width: 136px;
  height: 84px;
  line-height: 68px;
  border-radius: 8px;
  font-size: 30px;
  font-weight: 700;
  color: #fff;
  transition: .3s;
}

.currency a:hover {
  background: #00bdca;
  border: 8px solid #00bdca;
}

.graph {
  background: url("../../img/forecast.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50% 50%;
  height: 562px;
  margin-top: 42px;
  margin-left: 15px;
}
