//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }


// MONTSERRAT
@font-face {
	@include fontdefault(normal, 200, 'Montserrat');
	@include fontpath('Montserrat-ExtraLight', 'Montserrat-ExtraLight', 'Montserrat-ExtraLight'); }

@font-face {
	@include fontdefault(normal, 300, 'Montserrat');
	@include fontpath('Montserrat-Light', 'Montserrat-Light', 'Montserrat-Light'); }

@font-face {
	@include fontdefault(normal, 400, 'Montserrat');
	@include fontpath('Montserrat-Regular', 'Montserrat-Regular', 'Montserrat-Regular'); }

@font-face {
	@include fontdefault(normal, 500, 'Montserrat');
	@include fontpath('Montserrat-Medium', 'Montserrat-Medium', 'Montserrat-Medium'); }

@font-face {
	@include fontdefault(normal, 600, 'Montserrat');
	@include fontpath('Montserrat-SemiBold', 'Montserrat-SemiBold', 'Montserrat-SemiBold'); }

@font-face {
	@include fontdefault(normal, 800, 'Montserrat');
	@include fontpath('Montserrat-ExtraBold', 'Montserrat-ExtraBold', 'Montserrat-ExtraBold'); }
